@import "../variables/colors";
@import "../variables/typography";
@import "../variables/variables";
@import "../mixins/media";
@import "../mixins/utilities";

// ============================================
// Working Hours Section
// ============================================
.working-hours-section {
  padding: 40px 0;
  background: var(--white);

  @include lg-down {
    padding: 10px 0;
  }

  &__title {
    font-family: var(--font-family);
    font-size: $font-size-h1; // 48px
    font-weight: var(--font-weight-bold); // 700
    color: var(--primary); // #2B3B73
    margin: 0 0 40px;
    line-height: 1;

    @include lg-down {
      font-size: var(--font-size-h3); // 36px
      margin-bottom: 24px;
    }
  }

  &__cards {
    margin-bottom: 0;
  }

  // Desktop cards (3 separate cards)
  &__card {
    background: var(--primary); // #2B3B73
    border-radius: 12px;
    padding: 40px;
    height: 118px;
    display: flex;
    align-items: center;
  }

  &__card-content {
    display: flex;
    gap: 10px;
    align-items: center;
    width: 100%;
  }

  &__card-icon {
    font-size: 20px;
    color: var(--white);
    flex-shrink: 0;
  }

  &__card-text {
    display: flex;
    flex-direction: column;
    gap: 5px;
    flex: 1;

    @include lg-down {
      gap: 10px;
    }
  }

  &__card-time {
    font-family: var(--font-family);
    font-size: var(--font-size-body1); // 16px
    font-weight: var(--font-weight-bold); // 700
    color: var(--white);
    margin: 0;
    line-height: normal;

    @include lg-down {
      font-size: $font-size-body2; // 14px
    }
  }

  &__card-day {
    font-family: var(--font-family);
    font-size: $font-size-body3; // 12px
    font-weight: $font-weight-regular; // 400
    color: var(--white);
    margin: 0;
    line-height: normal;
  }

  // Mobile card (single card with all hours)
  &__card-mobile {
    background: var(--primary); // #2B3B73
    border-radius: 12px;
    padding: 40px 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__card-mobile-item {
    display: flex;
    gap: 10px;
    align-items: center;
  }
}

