    }
  }

  &__card-day {
    font-family: var(--font-family);
    font-size: $font-size-body3; // 12px
    font-weight: $font-weight-regular; // 400
    color: var(--white);
    margin: 0;
    line-height: normal;
  }

  // Mobile card (single card with all hours in subsection)
