// ============================================
// Working Hours Note
// ============================================
.working-hours-note {
  padding: 24px 0;
  background: var(--white);

  @include lg-down {
    padding: 16px 0;
  }

  p {
    font-family: var(--font-family);
    font-size: $font-size-body; // 18px
    font-weight: $font-weight-regular; // 400
    color: #ee1c23;
    margin: 0;
    line-height: normal;

    @include lg-down {
      font-size: var(--font-size-body1); // 16px
    }
  }
}

// ============================================
// Services Section
// ============================================
.services-section {
  padding: 40px 0;
  background: var(--white);

  @include lg-down {
    padding: 20px 0;
  }

  &__title {
    font-family: var(--font-family);
    font-size: $font-size-h1; // 48px
    font-weight: var(--font-weight-bold); // 700
    color: var(--primary); // #2B3B73
    margin: 0 0 20px;
    line-height: 1;

    @include lg-down {
      font-size: var(--font-size-h3); // 36px
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__item {
    display: flex;
    gap: 10px;
    align-items: center;
  }

  &__icon { 
    font-size: 20px;
    width: 20px;
    height: 20px;
    color: var(--info); // #3F9C35
    flex-shrink: 0;
  }

  &__text {
    font-family: var(--font-family);
    font-size: $font-size-h6; // 20px
    font-weight: $font-weight-medium; // 500
    color: var(--gray-1); // #212529
    margin: 0;
    line-height: normal;

    @include lg-down {
      font-size: $font-size-h4; // 24px
    }
  }
}
